import React from 'react'
import { Avatar, Card, CardHeader, CardActions, CardContent, CardMedia, IconButton, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { MoreVert, GitHub, Launch, MenuBook } from '@material-ui/icons';
import { red } from '@material-ui/core/colors';
import ProjectModal from './ProjectModal';

import noImage from '../img/imgNotAvail.jpg';

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  avatar: {
    backgroundColor: red[500],
  },
  centerCard: {
    alignContent: 'center',
    justifyContent: 'center',
    display: 'flex',

  }
}));



const DataCard = (item) => {
  const project = item.item;
  const classes = useStyles();

  const image = project.Image;

  console.log(image);

  const handleModal = () => {
    return (
      <ProjectModal project={project} />
    );
  }

  return (
    <React.Fragment>
      <div className={classes.centerCard}>

        <Card className={classes.root} onClick={handleModal}>
          <CardHeader
            avatar={
              <Avatar aria-label="project" className={classes.avatar}>
                {project.Type.charAt(0)}
              </Avatar>
            }
            action={
              <IconButton aria-label="settings">
                <MoreVert />
              </IconButton>
            }
            title={project.Title}
            subheader={project.Complete}
          />
          <CardMedia
            className={classes.media}
            image={image || noImage}
            title={project.Title}
          />
          <CardContent>
            <Typography variant="body2" color="textSecondary" component="p">
              {project.Description}
            </Typography>
          </CardContent>
          <CardActions disableSpacing>
            {project.GitHub !== "false" ?
              <IconButton className='iconButton' disableRipple aria-label="go to GitHub">
                <GitHub onClick={() => window.open(project.GitHub)} />
              </IconButton>
              : null
            }
            {project.GhPages !== "false" ?
              <IconButton aria-label="live project">
                <Launch onClick={() => window.open(project.GhPages)}/>
              </IconButton>
              : null
            }
            {project.Tutorial !== "false" ?
              <IconButton aria-label="tutorial">
                <MenuBook onClick={() => window.open(project.Tutorial)}/>
              </IconButton> 
              : null
            }
          </CardActions>

        </Card>
      </div>
    </React.Fragment>
  )
}

export default DataCard
