import React from "react";
import { Grid } from '@material-ui/core';
import ProjectData from './ProjectData';
import DataCard from './DataCard';

const Future = () => {

  return (
    <React.Fragment>
      <Grid container className='centering' spacing={3} >
        <Grid item xs={12} >
          <p className='PageHeader'>FUTURE</p>
        </Grid>
        {ProjectData.map((item) => (
          item.Complete === "false" ?
            <Grid item xs={12} sm={6} md={4} >
              <DataCard item={item} />
            </Grid>
            : null
          )
        )}
      </Grid>
    </React.Fragment>
  );
}

export default Future;