import React, { useState } from 'react';
import AboutMe from './AboutMe';
import Future from './Future';
import ProjectList from './ProjectList';
import Header from './Header';
import Footer from './Footer';
import Home from "./Home";
import { Grid } from '@material-ui/core';


const PortfolioControl = () => {
  const [page, setPage] = useState('Home')

  const changePage = newPage => {
    setPage(newPage);
  }

  let currentlyVisibleState = null;

  switch (page) {
    case 'AboutMe':
      currentlyVisibleState = <AboutMe />
      break;
    case 'ProjectList':
      currentlyVisibleState = <ProjectList />
      break;
    case 'Future':
      currentlyVisibleState = <Future />
      break;
    default:
      currentlyVisibleState = null
  }

  if (page === 'Home') {
    return (
      <React.Fragment>
        <Home onChangePage = {changePage}/>
      </React.Fragment>
    )
  } else {
    return (
      <React.Fragment>
        <div  id="contain">
          <Header onChangePage = {changePage}/>
          <Grid container className='centering'>
            <Grid item xs={10} >
              <div className='mainText'>
                {currentlyVisibleState}
              </div>
            </Grid>
          </Grid>
          <Footer />
        </div>
      </React.Fragment>
    )
  }
}

export default PortfolioControl;